
/*Topography*/
::selection{
    background: var(--color-primary);
    color:#fff;
}
html{
    font-size: 62.5%;
}

:root{
    --color-primary:#2584ff;
    --color-secondary: #00d9ff;
    --color-accent:#ff3400;
    --color-headings:#1b0760;
    --color-body:#918ca4;
    --color-body-darker:#5c5577;
    --color-border:#ccc;
    --border-radius:30px;
}

*, *::after, *::before{
    box-sizing: border-box;
}


body{
    font-family: Inter, Arial, Helvetica, sans-serif;
    color:var(--color-body);
    line-height: 1.5;
}



h1, h2, h3 {
    color:var(--color-headings);
    margin-bottom: 1rem;
    line-height: 1.1;
}

h1{
    font-size: 7rem;
}
h2{
    font-size: 4rem;
}
h3{
    font-size: 3rem;
    font-weight: 500;

}
p{
    font-size: 2.4rem;
    line-height: 1.5;
    margin-top: 0;
}
@media screen and (min-width:1024px){
    body{
        font-size: 1.8rem;
    }
    h1{
        font-size: 8rem;
    }
    h2{
        font-size: 4rem;
    }
    h3{
        font-size: 2.4rem;
    }
}

a{
    text-decoration: none;
}


/*Links*/
.link-arrow{
    color: var(--color-accent);
    text-transform: uppercase;
    font-size: 2rem;
    font-weight: bold;
    transition:margin 0.15s;
}
.link-arrow::after{
    content: "-->";
    margin-left: 5px;

}
.link-arrow:hover::after{
    margin-left: 10px; 
}

@media screen and (min-width:1024px){
    .link-arrow{
        font-size: 1.5rem;
    }
}


/*badges*/
.badge{
    border-radius: 20px;
    font-size: 2rem;
    padding: 0.5rem 0.2rem;
    white-space:nowrap;
    font-weight: 600;
}
.badge--primary{
    background-color: var(--color-primary);
    color:white;

}
.badge--secondary{
    background-color: var(--color-secondary);
    color:white;
}

.badge--small{
    font-size: 1.6rem;
    padding:0.5rem 1.5rem;
}

@media screen and (min-width:1024px){
   .badge{
    font-size: 1.5rem;
   }
   .badge--small{
    font-size: 1.2rem;
   }
}

/*lists*/
.list{
    list-style: none;
    padding-left: 0;

}

.list--inline .list__item{
    display:inline-block;
    margin-right: 2rem;
}

.list--tick{
    list-style-image: url(../icons/tick.svg);
    padding-left: 3rem;
    color: var(--color-headings);
}

.list--tick .list__item{
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}

@media screen and (min-width:1024px){
    .list--tick .list__item{
    padding-left: 0; 
}
}

/*icons*/
.icon{
    width:40px;
    height:40px;
}
.icon--primary{
    fill:var(--color-primary);
}

.icon--white{
    fill:white;   
}


.icon--small{
    width:30px;
    height:30px;
}

.icon-container{
  background:white;
  width:64px;
  height:64px;
  border-radius: 100%; 
  display: flex; 
  justify-content: center;
  align-items: center;
}

.icon-container--accent{
    background:var(--color-accent)
}

/*button*/
.btn{
    border-radius: 40px;
    border: 0;
    cursor: pointer;
    font-size: 1.8rem;
    font-weight: 600;
    padding: 2rem 3rem;
    text-transform: uppercase;
    text-align: center ;
    margin: 1rem 0;
    white-space: nowrap;
}

.btn .icon{
    width:2rem;
    height:2rem;
    margin-right: 1rem;
    vertical-align: middle
}

.btn--streched{
    padding-left: 6rem;
    padding-right: 6rem;

}
.btn--primary{
    background: var(--color-primary);
    color:white;
}
.btn--primary:hover{
    background: #3a8ffd;
}

.btn--secondary{
    background: var(--color-secondary);
    color:white;
}

.btn--secondary:hover{
    background: #00c8eb;
}

.btn--accent{
    background: var(--color-accent);
    color:white;
}
.btn--accent:hover{
    background: #ec3000;
}
.btn--block{
    display: inline-block;
    width:100%
}

.btn--outline{
    background: white;
    color:var(--color-headings);
    border:2px solid var(--color-headings);
}
.btn--outline:hover{
    background: var(--color-headings);
    color:white
}

@media screen and (min-width:1024px){
   .btn{
    font-size: 1.5rem;
   } 
}

/*Inputs*/
.input{
    border-radius: var(--border-radius);
    border:1px solid var(--color-border);
    color:var(--color-headings);
    font-size: 2rem;
    outline:0;
    padding: 1.5rem 3.5rem;

}

::placeholder{
  color:#cdcbd7;  
}

/*input group*/
.input-group{
    border:1px solid var(--color-border); 
    border-radius: var(--border-radius);
    display:flex;

}
.input-group .input{
    border:0;
    padding: 1.5rem 2rem;
    flex-grow: 1;
}

.input-group .btn{
    margin:4px;
}

@media screen and (min-width:1024px){
    .input{
        font-size: 1.5rem;;
    }
}

/*Cards*/
.card{
    border-radius: 7px;
    box-shadow: 0 0 20px 10px #f3f3f3;
    overflow: hidden;
}

.card__header, .card__body{
    padding:2rem 3rem;
}
.card--primary .badge--primary{
 background: #126de4;   
}
.card--primary .card__header{
    background: var(--color-primary);
    color: #fff;
}
.card--secondary .card__header{
    background:var(--color-secondary);
    color:#fff;
}

.card--secondary .badge--secondary{
    background: #02cdf1;
}

/*plans*/
.plan{
    transition: transform 0.2s ease-out;
}
.plan__name{
    color:#fff;
    margin: 0;
    font-weight: 500;
    font-size: 2.4rem;
}

.plan__price{
    font-size: 6rem;
}
.plan__billing-cycle{
    font-size: 2.4rem;
    font-weight: 300;
    opacity: 0.8;
    margin-right: 1rem;
}
.plan__description{
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
    display:block;
}

.plan .list__item{
    margin-bottom: 2rem;
}

/*popular badge*/
.card__header {
    position:relative
}
.popular{
    display: inline-block;
    width:40px;
    position:absolute;
    top:-6px;
    right:1%;
}
@media screen and (min-width:1024px){
    .plan__name{
        font-size: 1.4rem;
    }
    .plan__price{
        font-size: 5rem;
    }
    .plan__billing-cycle{
        font-size: 1.6rem;
    }
    .plan__description{
        font-size: 1.7rem;
    }
}

/*Media Object*/
.media{
    display:flex;
    margin-bottom: 4rem;

}

.media__title{
    margin-top: 0;
}
.media__body{
    margin: 0 2rem;
}

.media__image{
    margin-top: 1rem;
}

/*Quotes*/
.quote{
    font-size: 3rem;
    font-style: italic;
    color:var(--color-body-darker);
    line-height: 2.3rem;
}
.quote__text::before{
    content: open-quote;
}
.quote__text::after{
    content: open-quote;
}
.quote__author{
    font-size: 3rem;
    font-weight: 500;
    font-style: normal;
    margin-bottom: 0;
}

.quote__company{
    color:var(--color-headings);
    opacity: .4;
    font-size:2rem;
    font-style: normal;
}

.quote__line{
    position: relative;
    bottom: 10px;
}

@media screen and (min-width:1024px) {
    .quote{
        font-size: 2rem;
    }
    .quote__author{
        font-size:2.4rem;
    }
    .quote__company{
        font-size: 1.6rem;
    }
}


/*Grid*/
.grid{
    display:grid;
}
@media screen and (min-width: 768px){
    .grid--1x2{
        grid-template-columns: repeat(2,1fr);
    }
}

@media screen and (min-width:1024px){
    .grid--1x3{
        grid-template-columns: repeat(3,1fr);
    }
}

/*testomonial*/
.testomonial{
    padding:3rem;
}

.testimonial__image-container{
    position:relative;
}
.testimonial__image-container .testimonial__image{
    width:100%;
}

.testimonial__image-container .icon-container{
    position: absolute;
    top:3rem;
    right:-32px;
}

@media screen and (min-width:728px){
    .testimonial .quote,
    .testimonial .quote__author{
        font-size: 2.4rem;
    }
    .testimonial .quote{
        margin-left: 6rem;
    }
}

/*callout*/
.callout{
    padding:4rem;
    border-radius: 5px;
}

.callout--primary{
    background: var(--color-primary);
}
.callout__heading{
 color:#fff;
 margin-top: 0;
 font-size: 3rem;;
}

.callout .btn{
 justify-self: center; 
 align-self: center;  
}

.callout__content{
 text-align: center;
}

@media screen and (min-width:768px){
    .callout__content{
        text-align: left;
    }
    .callout .grid--1x2{
        grid-template-columns: 1fr auto;
    }
    .callout .btn{
        justify-self: start;
        margin:0 1rem;
    }

}

/*collapsible*/
.collapsible__header{
    display:flex;
    justify-content: space-between;

}
.collapsible__heading{
    margin-top: 0;
    font-size: 3rem;
}

.collapsible__chevron{
    transform:rotate(90deg);
    transition: transform 0.3s;
}

.collapsible__content{
    max-height: 0;
    overflow: hidden;
    opacity:0;
    transition: all 0.3s;
}

.collapsible--expanded .collapsible__chevron{
    transform: rotate(0deg);
}

.collapsible--expanded .collapsible__content{
    max-height: 100vh;
    opacity: 1;
}

/*blocks*/
.block{
    --padding-vertical:6rem;
    padding:var(--padding-vertical) 2rem;
    /*border:2px solid red;*/
}
.block__heading{
    margin-top: 0;
}
.block__header{
    text-align:center;
    margin-bottom: 4rem;
}
.block--dark{
    background: #000;
    color:#7b858b;
}
.block--dark h1,.block--dark h2,.block--dark h3{
    color:#fff;

}
.block--skewed-right{
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 90%);
}

.block--skewed-left{
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);
}

.container{
    max-width: 1140px;
    margin:0 auto;
}

/*Navigation bar*/
.nav{
    background: #000;
    display:flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding:0 1rem;
    align-items: center;
}

.nav__toggler{
    opacity: 0.5;
}

.nav.collapsible--expanded .nav__toggler{
    opacity: 1;
    box-shadow: 0 0 0 3px #666;
    border-radius: 5px;
    transition: box-shoadow 0.15s;
    cursor: pointer;

}

.nav__list{
    width: 100%;
    margin: 0;
}

.nav__item{
    padding: 0.5rem 2rem;
    border:2px solid #222;
}
.nav__item-link{
    color:#d2d0db;
    transition: color 0.3s;
}
.nav__item-link:hover{
    color:#fff;
}
.nav__brand{
    transform: translateX(5px);
}

@media screen and (min-width:768px){
    .nav__toggler{
        display: none;
    }
    .nav__list{
        width:auto;
        display:flex;
        font-size: 1.6rem;
        max-height: 100%;
        opacity: 1;

    }
    .nav__item{
        border:0;
    }
}

/*hero-banner*/
.hero{
    clip-path: polygon(0% 0%, 100% 0%, 100% 90%, 0% 100%);  
}
.hero__tagline{
   font-size: 2rem;
   color:#b9c3cf;
   letter-spacing: 1px;
   margin:2rem 0 5rem;
}

.hero__image{
    width:100%;
}

@media screen and (min-width:758px){
    .hero{
        padding-top:0;
    }
    .hero__content{
        text-align: left;
        align-self: center;
    }
}

/*Domain block*/
.block-domain .input-group{
    box-shadow:0 0 30px 20px #e6ebee;
    border:0;
    margin:4rem auto;
    max-width: 670px;
}
.block-domain_prices{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 6rem);
    font-size: 2rem;
    font-weight: 600;
    justify-items: center;
    max-width: 700px;
    margin: 0 auto;
}

@media screen and (min-width:768px){
    .block-domain_prices{
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }
}

/*plans block*/
.block-plans .grid{
    gap: 8rem 4rem;
}

.block-plans .card{
    max-width: 500px;
    margin:0 auto;
}
.plan--popular{
    transform:scale(1.1);
}

.plan:hover{
    transform:scale(1.05);
}
.plan--popular:hover{
    transform:scale(1.15);
}
@media screen and (min-width:768px){
    .block-domain_prices{
        grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    }
}

/*Featur Block*/
.feature__heading{
    font-size: 1rem;
}

.feature{
   gap:4rem 2rem; 
   margin:12rem 0;
}

.feature:first-of-type{
  margin-top: 6rem;  
}
.feature__image{
   width:100%;
}

/*Block Showcase*/
.block-showcase__image >img{
    width:100%;
}

@media screen and (min-width:768px){
    .block-showcase .grid{
        grid-template-columns: 50% 50%;;
    }
    .block-showcase__image{
        justify-self: end;
    }
    .block-showcase__image > img{
        width:auto;
        max-width: 700px;
    }
}
/*Footer*/
.footer{
    background: #232323;
}
.footer a {
    color:#777;

}

.footer a:hover{
    color:#fff;
    transition:color 0.3s;
}

.footer__section{
   padding:2rem;
   border:1px solid #393939;
}

.footer__heading{
   font-weight: 500;
   text-transform: uppercase;
   color: white;
}

.footer__brand{
    margin-top: 5rem;
    text-align: center;
}
.footer__copyright{
    font-size: 2.1rem;
    color: #fff;
    opacity:0.3;
}

@media screen and (min-width:768px){
    .footer__section{
        border: 0;
    }
    .footer__sections{
        grid-template-columns: repeat(auto-fit, minmax(10rem,1fr));
    }

    .footer .collapsible__chevron{
    display:none;
    } 

    .footer .collapsible__content{
        opacity: 1;
        max-height: 100%;
    }
    .footer__brand{
        margin-top: 5rem;
        text-align: center;
    }
    .footer__copyright{
        font-size: 2.1rem;
        color: #fff;
        opacity: 0.3;
       
    }
    .footer__heading{
        text-transform: uppercase;
        font-weight: 600;
        font-size: 2.5rem;
        line-height: 1.5;
    }
    .footer__section .list{
        margin:0;
    }

    .footer__brand img{
        width:100%;
        /*width of the image is 230px*/
        max-width: 230px;
    }
    
}

/* Sign-up Callout */
.callout-signup {
  transform: translateY(5rem);
}